'use client';
import { useState, useEffect } from 'react';
import { signIn } from 'next-auth/react';
import { useRouter } from 'next/navigation';

const SignIn = () => {
  const router = useRouter();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    // ‰ªÖÂú®ÂÆ¢Êà∑Á´ØÊ∏≤ÊüìÊó∂ËÆæÁΩÆ isClient ‰∏∫ true
    setIsClient(true);
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!isClient) return; // Á°Æ‰øù‰ªÖÂú®ÂÆ¢Êà∑Á´ØÊâßË°å

    const result = await signIn('credentials', {
      redirect: false,
      username,
      password,
    });

    if (result?.error) {
      setError('ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊÇ®ÁöÑÁî®Êà∑ÂêçÂíåÂØÜÁ†Å„ÄÇ');
    } else {
      // ÁôªÂΩïÊàêÂäüÔºåÈáçÂÆöÂêëÂà∞Áî®Êà∑È°µÈù¢
      router.push('/userpage');
    }
  };

  const handleQuickRegister = async () => {
    // ÁîüÊàêÈöèÊú∫Áî®Êà∑ÂêçÂíåÂØÜÁ†Å
    const randomUsername = `user${Math.floor(Math.random() * 100000)}`;
    const randomPassword = `pass${Math.floor(Math.random() * 100000)}`;

    // Ë∞ÉÁî®Ê≥®ÂÜå API
    try {
      const response = await fetch('/api/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username: randomUsername, password: randomPassword }),
      });

      if (response.ok) {
        alert(`Ê≥®ÂÜåÊàêÂäüÔºÅÁî®Êà∑Âêç: ${randomUsername}, ÂØÜÁ†Å: ${randomPassword}`);
        // Ëá™Âä®ÁôªÂΩï
        setUsername(randomUsername);
        setPassword(randomPassword);
        const fakeEvent = { preventDefault: () => {} } as React.FormEvent;
        handleSubmit(fakeEvent);
      } else {
        setError('Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑ÈáçËØï„ÄÇ');
      }
    } catch (error) {
      console.error('Ê≥®ÂÜåÈîôËØØ:', error);
      setError('Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑ÈáçËØï„ÄÇ');
    }
  };

  if (!isClient) {
    // Âú®ÊúçÂä°Âô®Á´ØÊ∏≤ÊüìÊó∂ËøîÂõû null ÊàñËÄÖ‰∏Ä‰∏™ÁÆÄÂçïÁöÑÂä†ËΩΩÊåáÁ§∫Âô®
    return null;
  }

  return (
    <div className="flex items-center justify-center min-h-screen p-4">
      <div className="p-8 max-w-md mx-auto bg-white rounded-xl shadow-md overflow-hidden relative">
        <button onClick={() => router.back()} className="absolute top-4 left-4 text-2xl text-yellow-500">üëà</button>
        <h1 className="text-3xl font-bold mb-6 text-center text-yellow-600">Ê¨¢ËøéÂõûÊù•</h1>
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label htmlFor="username" className="block text-sm font-medium text-gray-700">Áî®Êà∑Âêç:</label>
            <input
              type="text"
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
              className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-yellow-500 focus:border-yellow-500"
            />
          </div>
          <div className="mb-4">
            <label htmlFor="password" className="block text-sm font-medium text-gray-700">ÂØÜÁ†Å:</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-yellow-500 focus:border-yellow-500"
            />
          </div>
          {error && <p className="text-red-500">{error}</p>}
          <div className="flex items-center mb-4">
            <input type="checkbox" id="remember" className="mr-2" />
            <label htmlFor="remember" className="text-sm text-gray-700">ËÆ∞‰ΩèÊàë</label>
            <a href="#" className="ml-auto text-sm text-blue-500 hover:underline">ÂøòËÆ∞ÂØÜÁ†ÅÔºü</a>
          </div>
          <button type="submit" className="block w-full p-2 bg-blue-500 text-white rounded mt-4 hover:bg-blue-600 transition">ÁôªÂΩï</button>
        </form>
        <button onClick={handleQuickRegister} className="block w-full p-2 bg-green-500 text-white rounded mt-4 hover:bg-green-600 transition">‰∏ÄÈîÆÊ≥®ÂÜå</button>
        <p className="mt-4 text-center">
          ËøòÊ≤°ÊúâË¥¶Âè∑Ôºü <a href="/auth/signup" className="text-blue-500 hover:underline">ÁÇπÂáªÊ≥®ÂÜå</a>
        </p>
      </div>
    </div>
  );
};

export default SignIn;
